.tree-settings-btn {
	position: absolute;
	overflow: auto;
	top: 20px;
	right: 20px;
}

.tree-settings-card {
	position: absolute;
	top: 15px;
	right: 15px;
	min-height: 350px;
	width: 400px;
	box-shadow: 0 3px 6px -4px #0000001f, 0 6px 16px #00000014, 0 9px 28px 8px #0000000d;
}

.tree-settings-title {
	font-size: 16px;
}

.tree-settings-node-color {
	display: flex;
	justify-content: space-around;
	align-items: center;
	font-size: 16px;
	margin-top: 10px;
}

.tree-settings-colorBox {
	display: inline-block;
	width: 20px;
	height: 20px;
	cursor: pointer
}

.tree-settings-colorBox:hover {
	border: 2px solid yellow;
}