.treeStateInfo {
    position: absolute;
    top: 0;
    left: 0;
    width: 250px;
    overflow: auto;
    box-shadow: 0 3px 6px -4px #0000001f, 0 6px 16px #00000014, 0 9px 28px 8px #0000000d;
}

.treeStateInfo-list-item {
    padding: 4px 12px !important;
}

/* .treeStateInfo-list-item-button {
    float: right;
} */

.treeStateInfo-list-item-content {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}