.mainview {
	position: relative;
	height: 100%;
	width: 100%;
}

.node {
	cursor: pointer;
}

.node .ghost-circle {
	fill: rgba(70, 130, 196, .3);
	stroke: steelblue;
	stroke-width: 1px;
}

.node .node-circle {
	cursor: pointer;
}

.node text {
	font: 10px sans-serif;
}

.dragging {
	pointer-events: none;
	opacity: 0.5;
}

.cutting {
	pointer-events: none;
	opacity: 0.5;
}

.dragging-target {
	opacity: 0.5;
}

.depart-inner {
	/*opacity: 0.5;*/
	transition: opacity 0.45s;
}


.short-line {
	fill: none;
	stroke: steelblue;
	stroke-width: 1.5px;
	pointer-events: none;
}